.adding_form .skill{
  line-height: 2;
}

.buttons svg{
  width: 1.2rem;
  fill: #fff;
}

.buttons button{
  border: none;
  padding: 0.5rem 2rem;
  border-top-left-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  background-color: transparent;
  border: 2px solid rgba(0, 0, 0, 0.3);
  z-index: 1000;
  display: inline-block;
  position: relative;
  transition: all 0.3s;
}
.buttons button:hover{
  background-color: rgba(0, 0, 0, 0.2);
}

.link_bar{
  width: 80% !important;
  max-width: 100% !important;
  padding: 1rem 1rem;
  display: flex;
  margin: 1rem auto;
  align-items: center;
}

.link_bar .name svg{
  height: 1.4rem;
  fill: #fff
}

.icon_bg{
  display: inline-flex;
  width: 3rem;
  height: 3rem;
  align-items: center;
  justify-content: center;
  border-radius: 50%
}

.link_bar input{
  margin-bottom: 0;
  padding: 0;
  font-size: 1.4rem;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: none !important;
  border: none;
  position: relative;
  z-index: 1000
}

.link_bar .name{
  width: 14rem;
  text-align: left;
}



.link_bar .percentage{
  height: 2.5rem;
  position: relative;
  overflow: visible;
  width: calc(100% - 21rem);
}

.link_bar .percentage::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background-color: rgba(255, 255, 255, 0.15);
  z-index: 1001;
  border-radius: 0;
  box-shadow: none;
}

.link_bar .percentage input:focus{
  box-shadow: none !important;
}